body {
  max-width: 1440px;
}

section.wrap {
  position: relative;
  background-image: url(../img/back.png);
  background-repeat: no-repeat;
  height: 100vh;
  background-size: 720px;
  position: relative;

  .limited_wrapper {
    position: relative;
    display: flex;
    justify-content: space-around;

    .top_symbol {
      width: 21px;
      padding-left: 0px;
      padding-top: 37px;

      img {
        width: 100%;
      }
    }

    .left_col {
      width: 800px;
    }

    .yellow_part {
      margin-top: 32px;
      margin-left: 20px;
      background-image: url(../img/left.jpg);
      background-repeat: no-repeat;
      background-size: cover;
      height: 100%;
      max-width: 100%;
      position: relative;
      display: flex;
      z-index: 1;

      ul {
        list-style: none;

        li {
          font-family: "Geometria";
          font-style: italic;
          font-weight: 200;
          margin-left: 30px;
          padding-top: 60px;
        }
      }
    }
  }
}

.yellow_part:before {
  content: url("");
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: #ffff09ed;
  opacity: 0.9;
  z-index: -1;
}

.horizontal_num {
  -webkit-writing-mode: vertical-rl;
  -ms-writing-mode: tb-rl;
  writing-mode: vertical-rl;
  right: 140px;
  top: 56px;
  position: absolute;

  span {
    font-size: 28px;
    font-family: "Geometria";
    font-weight: 800;
    font-style: normal;
  }
}

.flex_hor {
  display: inline-flex;
  align-items: center;
  margin-left: 80px;
  padding-bottom: 0px;
}

.horizontal_text {
  margin-left: 107px;
  margin-top: 56px;
  writing-mode: vertical-rl;
  text-orientation: upright;

  p {
    font-size: 9px;
    font-family: "Geometria";
    font-weight: 200;
    font-style: italic;
  }
}

.square {
  width: 180px;

  img {
    width: 100%;
  }
}

.giometric_symbols {
  width: 310px;
  margin-left: 55px;
  margin-top: 52px;

  img {
    width: 100%;
  }
}

.navbar {
  margin-top: 90px;
}

.navbar-button {
  position: relative;
  display: block;
  margin-top: 42px;
}

.button {
  cursor: pointer;
  position: relative;

  i {
    font-size: 22px;
    position: absolute;
    right: -164px;
    top: 1px;
  }
}

.button:before {
  content: url(../img/button.png);
  position: absolute;
  right: -181px;
  top: -15px;
}

.overlay {
  position: fixed;
  background: #f7e611;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.35s, visibility 0.35s, height 0.35s;
  overflow: hidden;
  z-index: 10000;
}

.overlay.open {
  opacity: 1;
  visibility: visible;
  height: 100%;
}

.close-menu {
  display: block;
}

.overlay.open {
  ul {
    text-align: center;
    flex-direction: column;
    padding: 0;
    margin: 190px auto;
    height: 100%;
    width: 100%;

    li {
      padding-bottom: 20px;

      a {
        text-decoration: none;
      }
    }
  }
}

.top_gio {
  width: 224px;
  position: absolute;
  z-index: 1;
  right: 264px;
  top: 75px;
}

.symbol {
  position: absolute;
  left: 635px;
  width: 150px;
  z-index: 1;
  bottom: -138px;

  img {
    width: 100%;
  }
}

.symbol:after {
  content: url("");
  position: absolute;
  display: block;
  width: 210px;
  height: 9px;
  background-color: #e6d82e;
  top: 112px;
  left: -17px;
}

.giometric_right {
  position: absolute;
  width: 287px;
  right: 112px;
  bottom: -250px;
}

