.ural {
  border-radius: 50%;
  border: 1px solid #000;
  margin: 10px;
  margin-top: 60px;
  padding: 0 10px;

  p {
    font-size: 17px;
    font-family: "Geometria";
    font-weight: 400;
    color: #fff;
  }
}
.slider_container{
  overflow: hidden;
  
}
.slider_wrap{
  background-color:#f7e611;
  display: flex;
  height: 380px;
  width: 140px;
  transition: 2s;
}
.slide {
  z-index: 1;
  display: none;
  min-width: 140px;
  height: 380px;

  span {
    font-size: 45px;
    font-family: "Geometria";
    font-weight: 800;
    text-align: center;
    margin-top: 90px;
  }

  span:last-child {
    padding-left: 43px;
  }
}

